@import "../../../scss/sass_vars";

.projectPageWrapper {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.projectPage {
  position: relative;
  display: grid;
  grid-template-areas:
    "widgetSettings widgetSettings"
    "knowledgeBase knowledgeBase"
    "metadata clientScript";
  gap: 20px;

  @media screen and (max-width: $mobileBreakpoint) {
    display: flex;
    flex-direction: column;
    gap: 20px;
  }

  & > div:nth-child(1) {
    grid-area: widgetSettings;
  }
  & > div:nth-child(2) {
    grid-area: knowledgeBase;
  }
  & > div:nth-child(3) {
    grid-area: metadata;
  }
  & > div:nth-child(4) {
    grid-area: clientScript;
  }

  label {
    padding: 5px;
    border-radius: var(--border-radius);
    background-color: var(--main-background-color-alpha);
    user-select: none;
  }
}

.projectHeader {
  display: flex;
  justify-content: space-between;
}

.projectPageSection {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  border-radius: var(--border-radius);
  border: 1px solid var(--color-border-main);
  background-color: var(--main-background-color-alpha);
  transition: background-image 250ms linear;
  box-shadow: 3px 3px 8px 2px var(--main-text-color);
  backdrop-filter: blur(2px);

  .sectionHeader {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 10px;
    padding: 7px 15px;
    border-radius: var(--border-radius) 0;
    background-image: var(--bg-linear-gradient);
    background-color: var(--main-background-color-alpha);

    & > :first-child {
      margin-right: auto;
    }
  }

  .sectionContent {
    padding: 15px;
  }
}

.widgetSettings {
  display: flex;
  gap: 20px;
  min-height: 220px;

  @media screen and (max-width: $mobileBreakpoint) {
    flex-direction: column;
  }

  & > div {
    flex-basis: 50%;
  }

  .widgetControls {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    label {
      margin-bottom: 10px;
    }

    .color {
      width: 25%;

      input {
        width: 100%;
      }
    }

    .sourceURL {
      width: 100%;
    }

    .range {
      width: 45%;
    }
  }

  .demoWidgetWrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .stylesWarning {
    color: var(--color-error);
    &::before {
      content: "*";
    }
  }
}

.knowledgeBase {
  display: flex;
  gap: 20px;

  @media screen and (max-width: $mobileBreakpoint) {
    flex-direction: column;
  }

  & > div {
    flex-basis: 50%;
  }

  .labelWrapper {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }

  .clearList {
    color: var(--color-accent);
    text-decoration: underline;
    cursor: pointer;

    &:hover {
      text-decoration: none;
    }
  }

  .knowledgeBaseList {
    max-height: 200px;
    overflow-y: auto;
  }

  input {
    display: none;
  }

  .knowledgeBaseUploadForm {
    position: relative;
    height: 200px;
    padding: 5px;
    border: 5px dashed var(--color-border-main);
    border-radius: 10px;
    overflow-x: hidden;
    overflow-y: auto;

    input {
      position: absolute;
      top: 0;
      width: 100%;
      height: 95%;
      opacity: 0;
      background-color: #fff;
    }
  }
}

.filesList {
  height: 100%;
  overflow: auto;
  list-style: none;

  li {
    display: flex;
    justify-content: space-between;
  }
}

.scriptCode {
  width: 100%;
  display: block;
  overflow-x: auto;
  white-space: pre;
}

summary {
  margin-bottom: 15px;
}

.buttonsWrapper {
  display: flex;
  gap: 10px;
}

.dragndropMessage {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  pointer-events: none;
}
