.projectTile {
  display: flex;
  padding: 10px;
  background-image: var(--bg-linear-gradient);
  background-color: var(--main-background-color-alpha);
  color: var(--main-text-color);
  border-radius: 5px;
  border: 1px solid var(--color-border-main);
  box-shadow: 3px 3px 8px 2px var(--main-text-color);
  -webkit-backdrop-filter: blur(2px);
  backdrop-filter: blur(2px);
}

.projectDetails {
  flex-grow: 1;
  cursor: pointer;

  .wrapper {
    width: fit-content;
  }

  .projectName {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-weight: bold;
    font-size: 20px;
    margin-bottom: 10px;
  }
  .projectDetailField {
    color: var(--main-text-color-alpha);
    font-size: 16px;
  }
}

.projectControls {
  display: flex;
  align-items: center;
}
